import React, { useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import styled from 'styled-components';
import artifactsData from './artifactsData';
import artifactsDataJson from './artifactsData.json'; // Import the artifact data

// Styled components
const DetailContainer = styled.div`
  padding: 20px;
`;

const Header = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 20px;
`;

const ArtifactTitle = styled.h1`
  font-size: 24px;
  margin: 0;
`;

const ArtifactImage = styled.img`
  width: 80px;
  height: 80px;
  border-radius: 10px;
  margin-right: 20px;
`;

const ArtifactInfo = styled.div`
  display: flex;
  align-items: center;
  margin-top: 10px;
`;

const Badge = styled.img`
  padding: 0% 0%.5;
  transform: scale(0.8);
`;

const StarRating = styled.div`
  display: flex;
  align-items: center;
  margin-top: 10px;
  cursor: pointer;
`;

const Star = styled.img`
  width: 20px;
  height: 20px;
  margin-right: 5px;
`;

const ArtifactDescription = styled.p`
  font-size: 14px;
  color: #666;
`;

const OptionText = styled.p`
  font-size: 16px;
  margin: 10px 0;
`;

const DescriptionText = styled.div`
  font-size: 14px;
  color: #666;
  margin-top: 10px;
`;

const Divider = styled.hr`
  margin: 20px 0;
`;

const Navigation = styled.div`
  font-size: 14px;
  margin-bottom: 20px;
  a {
    text-decoration: none;
    color: #007bff;
    &:hover {
      text-decoration: underline;
    }
  }
`;

const ArtifactCard = styled.img`
  width: 20%;
  margin-top: 5%;
  align-items: center;
  justify-content: center;
  border-radius: 5%;
`;

const ArtifactsDetailPage = () => {
  const { artifactName } = useParams();
  const artifactInfo = artifactsDataJson[artifactName] && artifactsDataJson[artifactName][0];
  const artifact = artifactInfo ? artifactsData.find(a => a.name === artifactInfo.name) : null;

  const [starRating, setStarRating] = useState(artifactInfo ? artifactInfo.star : 1);

  if (!artifactInfo || !artifact) {
    return <DetailContainer>아티팩트 정보를 찾을 수 없습니다.</DetailContainer>;
  }

  const handleStarClick = (index) => {
    setStarRating(index + 1);
  };

  const { name, optionText, options, abilities, description } = artifactInfo;
  
  // Split the ability values
  const [firstValue, secondValue] = abilities.values[starRating - 1].split('/');
  const abilityText = abilities.text.replace('{value}', firstValue).replace('{value}', secondValue);

  const optionValue = options[starRating - 1];

  return (
    <DetailContainer>
      <Navigation>
        <Link to="/artifacts/info">아티펙트</Link> &gt; {name}
      </Navigation>
      <Header>
        <ArtifactImage src={`https://res.cloudinary.com/dyc4ijymg/image/upload/f_auto,q_auto/v1/artifacts/Thumbnail_${artifact.path}`} alt={name} />
        <div>
          <ArtifactTitle>{name}</ArtifactTitle>
          <ArtifactInfo>
            <Badge src={`https://sseong-mi.github.io/Tower-of-Adventures/images/grades/${artifact.grade}.png`} />
            {artifact.type !== 'none' && (
              <Badge src={`https://sseong-mi.github.io/Tower-of-Adventures/images/types/${artifact.type}.png`} />
            )}
          </ArtifactInfo>
          <StarRating>
            {[...Array(5)].map((_, i) => (
              <Star
                key={i}
                src={
                  i < starRating
                    ? 'https://sseong-mi.github.io/Tower-of-Adventures/images/level_items/star_filled.png'
                    : 'https://sseong-mi.github.io/Tower-of-Adventures/images/level_items/star_empty.png'
                }
                alt={`${i + 1} star`}
                onClick={() => handleStarClick(i)}
              />
            ))}
          </StarRating>
        </div>
      </Header>
      <OptionText>기본 옵션</OptionText>
      <ArtifactDescription>{optionText} +{optionValue}%</ArtifactDescription>
      <OptionText>고유 능력</OptionText>
      <ArtifactDescription dangerouslySetInnerHTML={{ __html: abilityText }} />
      <Divider />
      <DescriptionText dangerouslySetInnerHTML={{ __html: description }} />
      <ArtifactCard src={`https://res.cloudinary.com/dyc4ijymg/image/upload/f_auto,q_auto/v1/artifacts/Texture_${artifact.path}`}/>
    </DetailContainer>
  );
};

export default ArtifactsDetailPage;
